.pdf-text-modifier-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.pdf-text-modifier-panel {
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  width: 90vw;
  max-width: 600px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid #e0e0e0;
  background: #f8f9fa;
  border-radius: 8px 8px 0 0;

  h3 {
    margin: 0;
    font-size: 18px;
    font-weight: 600;
    color: #333;
  }

  .close-btn {
    background: none;
    border: none;
    padding: 4px;
    cursor: pointer;
    color: #666;
    font-size: 16px;
    border-radius: 4px;

    &:hover {
      background: #e9ecef;
      color: #333;
    }
  }
}

.panel-content {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;

  h4 {
    margin: 0;
    font-size: 16px;
    font-weight: 600;
    color: #555;
  }

  .add-btn {
    background: #007bff;
    color: white;
    border: none;
    padding: 6px 12px;
    border-radius: 4px;
    font-size: 12px;
    cursor: pointer;

    &:hover {
      background: #0056b3;
    }

    i {
      margin-right: 4px;
    }
  }
}

.editors-section {
  margin-bottom: 24px;

  .editors-list {
    border: 1px solid #e0e0e0;
    border-radius: 6px;
    max-height: 200px;
    overflow-y: auto;
  }

  .editor-item {
    display: flex;
    align-items: center;
    padding: 8px 12px;
    border-bottom: 1px solid #f0f0f0;
    cursor: pointer;
    transition: background-color 0.2s;

    &:last-child {
      border-bottom: none;
    }

    &:hover {
      background: #f8f9fa;
    }

    &.active {
      background: #e3f2fd;
      border-color: #2196f3;
    }

    .editor-preview {
      flex: 1;
      
      span {
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: block;
      }
    }

    .delete-btn {
      background: none;
      border: none;
      color: #dc3545;
      padding: 4px 6px;
      border-radius: 4px;
      cursor: pointer;
      font-size: 12px;
      opacity: 0.7;

      &:hover {
        opacity: 1;
        background: #f8d7da;
      }
    }
  }

  .no-editors {
    text-align: center;
    padding: 32px 16px;
    color: #666;

    p {
      margin: 0;
      font-size: 14px;
    }
  }
}

.properties-section {
  .property-group {
    margin-bottom: 16px;

    label {
      display: block;
      margin-bottom: 6px;
      font-size: 14px;
      font-weight: 500;
      color: #555;
    }

    textarea, select, input[type="text"] {
      width: 100%;
      padding: 8px 12px;
      border: 1px solid #ccc;
      border-radius: 4px;
      font-size: 14px;
      font-family: inherit;

      &:focus {
        outline: none;
        border-color: #007bff;
        box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
      }
    }

    textarea {
      resize: vertical;
      min-height: 60px;
    }

    &.half {
      width: calc(50% - 8px);
    }
  }

  .property-row {
    display: flex;
    gap: 16px;
  }

  .color-input-group {
    display: flex;
    gap: 8px;

    .color-input {
      width: 50px;
      height: 36px;
      border: 1px solid #ccc;
      border-radius: 4px;
      cursor: pointer;
    }

    .color-text {
      flex: 1;
    }
  }

  .style-buttons {
    display: flex;
    gap: 4px;

    button {
      padding: 8px 12px;
      border: 1px solid #ccc;
      border-radius: 4px;
      background: white;
      cursor: pointer;
      transition: all 0.2s;

      &:hover {
        background: #f8f9fa;
      }

      &.active {
        background: #007bff;
        color: white;
        border-color: #007bff;
      }

      i {
        font-size: 14px;
      }
    }
  }

  .range-input {
    width: 100%;
    margin-top: 4px;
  }
}

.no-selection {
  text-align: center;
  padding: 32px 16px;
  color: #666;
  background: #f8f9fa;
  border-radius: 6px;
  border: 1px dashed #ccc;

  p {
    margin: 0;
    font-size: 14px;
  }
}

.panel-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-top: 1px solid #e0e0e0;
  background: #f8f9fa;
  border-radius: 0 0 8px 8px;

  .footer-left, .footer-right {
    display: flex;
    gap: 8px;
  }

  button {
    padding: 8px 16px;
    border-radius: 4px;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.2s;

    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }
  }

  .clear-btn {
    background: #dc3545;
    color: white;
    border: 1px solid #dc3545;

    &:hover:not(:disabled) {
      background: #c82333;
    }

    i {
      margin-right: 4px;
    }
  }

  .cancel-btn {
    background: white;
    color: #666;
    border: 1px solid #ccc;

    &:hover {
      background: #f8f9fa;
    }
  }

  .apply-btn {
    background: #28a745;
    color: white;
    border: 1px solid #28a745;

    &:hover:not(:disabled) {
      background: #218838;
    }

    i {
      margin-right: 4px;
    }
  }
}

// 반응형 디자인
@media (max-width: 768px) {
  .pdf-text-modifier-panel {
    width: 95vw;
    max-height: 90vh;
  }

  .property-row {
    flex-direction: column;
    
    .property-group.half {
      width: 100%;
    }
  }

  .panel-footer {
    flex-direction: column;
    gap: 12px;
    
    .footer-left, .footer-right {
      width: 100%;
      justify-content: center;
    }
  }
}